<template>
    <div class="search" id="searchPage">
        <div class="searchHeader">
            <div class="searchBorder">
                <input @keypress.enter="searchToList" id="searchEnter" class="searchEnter" type="text"
                       placeholder="搜索品牌/车系" v-model="searchConetnt" :value="searchConetnt" maxlength="10">
                <img class="deleSearch" @click="deleSearchCount" src="./close.png" alt="">
            </div>
            <span class="returnIndex" @click="cancle" alt="返回">取消</span>
        </div>
        <div class="searchWrapper">
            <div class="showMatching" v-if="isShowMatching">
                <ul>
                    <li v-for="item in allBrandCars">
                        <template v-if="item.brandGroupName">
                            <p @click="brandGroupIdToList(item.brandGroupId,item.brandGroupName)">
                                {{item.brandGroupName}}</p>
                        </template>
                        <p v-for="xi in item.types" :id="xi.typeId" @click="typeIdToList(xi.typeId,xi.typeFullName)">
                            {{xi.typeFullName}}</p>
                    </li>
                </ul>
            </div>
        </div>

        <div id="searchHistoryBorder" v-if="ifshowHistory">
            <div class="historySearchBorder">
                <p class="hotText" style="background:#f5f5f5;color:#000;">历史搜索</p>
                <img class="clearHistory" src="./clearHistory.png" @click="clearHistory" alt="">
            </div>
            <dl class="searchHistory clearfix">
                <dd v-for="(item,index) in historyCarList"><span @click="historyTo(item)">{{item}}</span></dd>
            </dl>
        </div>

        <p class="hotText">热门搜索</p>
        <ul class="hotCarList">
            <li v-for="item in hotCarList" @click="hotToList(item)">
                {{item.brandGroupName}}
            </li>
        </ul>

        <router-view></router-view>
    </div>
</template>

<script>
    export default {
        data() {
            return {
                allBrandCars: [],
                hotCarList: [],
                historyCarList: [],
                searchConetnt: '',
                ifshowHistory: true,
                //isNoData:false,
                isShowMatching: false,
                nativeHref: ''
            }
        },
        methods: {
            cancle() {
                this.$router.push('/');
            },
            torepeat(arr) {
                var re = [];
                for (var i = 0; i < arr.length; i++) {
                    if (re.indexOf(arr[i]) == -1) {
                        re.push(arr[i]);
                    }
                }
                return re;
            },
            searchToList() {
                var _this = this;
                this.$router.push('/list?value='+_this.searchConetnt)
                var storage = window.localStorage;
                if (_this.searchConetnt == '') {
                    return false;
                } else {
                    var arrDisplay = new Array();
                    var strStoreDate = storage.getItem("carHistory");
                    if (strStoreDate || strStoreDate.length) {
                        arrDisplay = strStoreDate.split(',');
                        arrDisplay = _this.torepeat(arrDisplay);
                    }
                    arrDisplay.unshift(_this.searchConetnt);
                    storage.setItem("carHistory", arrDisplay);
                }
            },
            historyTo(msg) {
                this.$router.push('/list?value='+msg)
            },
            clearHistory() {
                window.localStorage.setItem("carHistory", '')
                this.ifshowHistory = false;
            },
            deleSearchCount() {
                this.isShowMatching = false;
                setTimeout(function () {
                    document.getElementById('searchEnter').value = "";
                }, 0)
            },
            firstGetHistory() {
                var showHistory = window.localStorage.getItem("carHistory");
                if (!showHistory || showHistory.length == 0) {
                    window.localStorage.setItem("carHistory", '')
                    this.ifshowHistory = false;
                } else {
                    this.historyCarList = showHistory.split(',');
                    this.historyCarList = this.torepeat(this.historyCarList);
                    if (this.historyCarList.length >= 5) {
                        this.historyCarList.length = 5;
                    }
                }
            },
            dim() {
                var _this = this;
                var searchname = $('.searchEnter').val();
                var citys = window.localStorage.getItem("city");
                var reg = new RegExp('"', "g");
                citys = citys.replace(reg, "");
                citys = Number(citys);
                if (searchname !== '') {
                    var getCity = {city: citys, searchName: searchname};
                    $.ajax({
                        type: "POST", url: this.global.host + "/model/branAndTypeOfCityAndSearchName",
                        data: getCity, success: function (response) {
                            if (response.resultCode == '0000') {
                                _this.allBrandCars = response.result;
                                if (_this.allBrandCars.length != 0) {
                                    _this.isShowMatching = true;
                                } else {
                                    _this.isShowMatching = false;
                                }
                            } else if (response.resultCode == '1111') {
                                _this.isShowMatching = false;
                            }
                        }
                    });
                } else {
                    _this.isShowMatching = false;
                }
            },
            brandGroupIdToList(id, name) {
                var _this = this;
                var city = window.localStorage.getItem("city"), reg = new RegExp('"', "g");
                city = city.replace(reg, "");
                city = Number(city);
                var data = {},
                    token = '',
                    storage = window.localStorage;
                if (storage.getItem("user")) {
                    token = JSON.parse(storage.getItem("user")).token;
                }
                var data = {brandGroupId: id, token: token, city: city};
                $.ajax({
                    type: "POST", url: this.global.host + "/inventory/vehicleList",
                    data: data, success: function (response) {
                        if (response.resultCode == '0000') {
                            _this.$router.push('/list?brandGroupId='+id+'&&brand_group_name='+name);
                            var storage = window.localStorage;
                            var arrDisplay = new Array();
                            var strStoreDate = storage.getItem("carHistory");
                            if (strStoreDate || strStoreDate.length) {
                                arrDisplay.push(strStoreDate);
                            }
                            arrDisplay.unshift(name);
                            storage.setItem("carHistory", arrDisplay);

                        } else if (response.resultCode == '1111') {
                            _this.isNoData = true;
                            _this.isShowMatching = false;
                        }
                    }
                });
            },
            typeIdToList(id, name) {
                var _this = this;
                var city = window.localStorage.getItem("city"), reg = new RegExp('"', "g");
                city = city.replace(reg, "");
                city = Number(city);
                var data = {},
                    token = '',
                    storage = window.localStorage;
                if (storage.getItem("user")) {
                    token = JSON.parse(storage.getItem("user")).token;
                }
                var data = {typeId: id, token: token, city: city};
                $.ajax({
                    type: "POST", url: this.global.host + "/inventory/vehicleList",
                    data: data, success: function (response) {
                        if (response.resultCode == '0000') {

                            _this.$router.push('/list?brandGroupId='+id+'&&brand_group_name='+name);

                            var storage = window.localStorage;
                            var arrDisplay = new Array();
                            var strStoreDate = storage.getItem("carHistory");
                            if (strStoreDate || strStoreDate.length) {
                                arrDisplay.push(strStoreDate);
                            }
                            arrDisplay.unshift(name);
                            storage.setItem("carHistory", arrDisplay);

                        } else if (response.resultCode == '1111') {
                            _this.isNoData = true;
                            _this.isShowMatching = false;
                        }
                    }
                });
            },
            hotToList(item) {
                var _this = this;
                _this.$router.push('/list?brandGroupId='+item.brandGroupId+'&&brand_group_name='+item.brandGroupName);
                //   var name = item.brandGroupName;
                //   var storage = window.localStorage;
                //   if(name == ''){
                //     return false;
                //   }else{
                //     var arrDisplay = new Array();
                //     var strStoreDate = storage.getItem("carHistory");
                //     if(strStoreDate || strStoreDate.length){
                //       arrDisplay.push(strStoreDate);
                //     }
                //     arrDisplay.unshift(name);
                //     storage.setItem("carHistory", arrDisplay);
                //   }
            }
        },
        mounted() {
            var _this = this;

            this.firstGetHistory();
            var citys = window.localStorage.getItem("city");
            var reg = new RegExp('"', "g");
            citys = citys.replace(reg, "");
            var getCity = {city: citys};

            //获取品牌
            $.ajax({
                type: "POST", url: this.global.host + "/model/brandGroupListByCity",
                data: getCity, success: function (response) {
                    if (response.resultCode == '0000') {
                        _this.hotCarList = response.result.hotBrandGroupList;
                    } else if (response.resultCode == '1111') {
                    }
                }
            });

            $('#searchEnter').bind('input propertychange', function () {
                _this.dim();
            });

        }
    }
</script>

<style lang="scss" scoped>
    .search {
        position: absolute;
        background: #eee;
        width: 7.5rem;
        height: 100%;
        .searchHeader {
            height: .88rem;
            background: #fff;
            display: flex;
            position: relative;
            .returnIndex {
                height: 1rem;
                line-height: 1rem;
                font-size: .28rem;
                font-size: .3rem;
            }
            .title {
                font-size: .34rem;
                color: #ffffff;
            }
        }
        .searchBorder {
            width: 5.8rem;
            height: .54rem;
            background: #f6f6f6;
            border-radius: 20px;
            overflow: hidden;
            line-height: .54rem;
            position: relative;
            margin: .22rem .3rem 0 .4rem;
            .searchEnter {
                width: 5rem;
                height: .34rem;
                line-height: .34rem;
                padding: .1rem 0;
                background: #f6f6f6;
                font-size: .22rem;
                text-indent: 2em;
                caret-color: #ffb900;
                font-family: '微软雅黑';
            }
            .deleSearch {
                position: absolute;
                right: .2rem;
                top: .13rem;
                width: .3rem;
                height: .3rem;
            }
        }
        .searchWrapper {
            display: flex;
            background-color: #ffffff;
            position: relative;

            .toSearch {
                width: 1.3rem;
                height: .74rem;
                background: #F6A32B;
                color: #ffffff;
                line-height: .74rem;
                border-radius: 5px;
                margin-left: .1rem;
                text-align: center;
                margin-top: .16rem;
                font-size: .3rem;
                display: block;
            }
            .showMatching {
                width: 100%;
                background: #fff;
                position: absolute;
                left: 0;
                top: 0;
                border-top: none;
                z-index: 8;
                ul {
                    width: 100%;
                    overflow-y: auto;
                    text-indent: .5em;
                    border-top: 1px solid #f2f2f2;
                    margin-top: .2rem;
                    background: #fff;
                    min-height: 10rem;
                    p {
                        height: .8rem;
                        line-height: .8rem;
                        color: #646464;
                        font-size: .26rem;
                        border-bottom: 1px solid #f2f2f2;
                        width: 90%;
                        margin: 0 auto;
                    }
                }
            }
        }
        .hotText {
            padding: .3rem 0 .2rem .28rem;
            background-color: #ffffff;
            color: #666;
            font-size: .25rem;
        }

        .hotCarList {
            display: flex;
            flex-flow: wrap;
            background: white;
            padding-bottom: .3rem;
            margin-bottom: .1rem;
            li {
                margin-left: 4%;
                display: flex;
                justify-content: center;
                align-items: center;
                font-size: .26rem;
                margin-top: .2rem;
                border-radius: 20px;
                width: 1.5rem;
                height: .58rem;
                background: #f6f6f6;
                color: #666;
                -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
                a {
                    color: #666666;
                    text-decoration: none;
                    -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
                }
            }
        }
        #searchHistoryBorder {
            background: #f5f5f5;
            padding-bottom: .26rem;
            .historySearchBorder {
                display: flex;
                justify-content: space-between;
                .clearHistory {
                    width: .24rem;
                    height: .24rem;
                    color: #F6A32B;
                    margin-top: .3rem;
                    margin-right: .46rem;
                    color: #000;
                }
            }
        }
        .searchHistory {
            width: 100%;
            height: auto;
            display: flex;
            flex-wrap: wrap;
            dd {
                float: left;
                width: auto;
                height: .6rem;
                line-height: .6rem;
                color: #000;
                background: #fff;
                text-align: center;
                font-size: .28rem;
                border-radius: 20px;
                margin: .1rem 0 .2rem .74rem;
                min-width: 1.5rem;
            }
        }
    }

    .noData {
        width: 7.5rem;
        text-align: center;
        img {
            width: 3.52rem;
        }
        p {
            font-size: .3rem;
            margin-top: .2rem;
            padding-bottom: .36rem;
            border-bottom: 10px solid #ededed;
        }
    }
</style>



